import { getActorId } from "./ActorID";
import { getPackId, loadPack } from "./PackId";
import { loadSUuid } from "./UuidHelper";
export function isPackItem(id) {
    return id.packId;
}
export function isOwnedItem(id) {
    return id.actorId;
}
export async function getItem(id) {
    if (isPackItem(id)) {
        let pack = await loadPack(id.packId);
        return pack?.find(x => x.id === id.itemId);
    }
    else {
        return loadSUuid(id.uuid);
    }
}
export async function getItems(ids, skipNotFound = true) {
    let items = [];
    for (let id of ids) {
        let item = await getItem(id);
        if (item) {
            items.push(item);
        }
        else if (!skipNotFound) {
            throw new Error("Item with id [" + JSON.stringify(id) + "] not found.");
        }
    }
    return items;
}
export function getItemId(item) {
    let base = { itemId: item.id, uuid: item.uuid };
    if (item.compendium) {
        return { ...base, packId: getPackId(item.compendium) };
    }
    else if (item.actor) {
        return { ...base, actorId: getActorId(item.actor) };
    }
    else {
        return base;
    }
}
