export class Queue {
    jumpSorter;
    array = [];
    constructor(jumpSorter) {
        this.jumpSorter = jumpSorter;
    }
    add(jumps) {
        this.array.push(...jumps);
        this.array.sort(this.jumpSorter);
    }
    next() {
        if (this.array.length == 0)
            return undefined;
        return this.array.splice(0, 1)[0];
    }
}
