import { getItem, isPackItem } from "../../Util/Identifiers/ItemID";
import getFlag from "../../Util/Helper/FlagHelper";
import { isEqual } from "../../Util";
const defaultSatchelFlags = {
    itemData: [],
    ids: [],
    filter: "Any"
};
export function getSatchelFlag(item) {
    return getFlag(item, "Satchel", defaultSatchelFlags);
}
function idQty(itemData, qty) {
    return {
        ...itemData,
        data: {
            ...itemData.data,
            quantity: qty
        }
    };
}
function removeItem(satchel, itemId, qty) {
}
export async function addItemToSatchel(satchel, itemId, qty) {
    let [flag, setFlag] = satchel;
    let item = (await getItem(itemId));
    if (!isPackItem(itemId)) {
        let newData = idQty(item.data, qty);
        await setFlag({
            ...flag,
            itemData: [
                ...(flag.itemData || []),
                newData
            ]
        });
    }
    else {
        let existing = (flag.ids || []).find(x => isEqual(x.id, itemId));
        let newArr;
        if (existing) {
            newArr = (flag.ids || []).filter(x => x !== existing);
            newArr.push({
                ...existing,
                qty: qty + existing.qty
            });
        }
        else {
            newArr = [
                ...(flag.ids || []),
                {
                    id: itemId,
                    name: item.name,
                    qty
                }
            ];
        }
        await setFlag({
            ...flag,
            ids: newArr
        });
    }
}
export async function getSatchelItems(satchel) {
    let [flag] = satchel;
    let items = [];
    for (let id of (flag.ids || [])) {
        let item = (await getItem(id.id));
        let newId = idQty(item.data, id.qty);
        items.push((await Item.create(newId, { temporary: true })));
    }
    if (flag.itemData) {
        for (let data of (flag.itemData)) {
            items.push((await Item.create(data, { temporary: true })));
        }
    }
    return items;
}
