import React from "react";
import { usePromise } from "../../Helper/PromiseHelper";
import { CircularProgress, Tooltip } from "@material-ui/core";
export function ItemControl({ control: { title, onClick, text, classes } }) {
    return (<Tooltip title={title}>
            <a className={`item-control $classes`} onClick={(e) => {
            e.stopPropagation();
            e.preventDefault();
            onClick();
        }}>
                {text}
            </a>
        </Tooltip>);
}
export function getEditControl(item) {
    return {
        title: "Edit Item",
        classes: "item-edit",
        text: <i className="fas fa-edit"/>,
        onClick: () => item.sheet.render(true, { editable: item.isOwner })
    };
}
export function generateControlsColumn(controlGetter, dependants) {
    return {
        title: "",
        getter: (columnArgs) => <ItemControls controls={controlGetter} args={columnArgs} dependants={dependants || []}/>
    };
}
function ItemControls({ controls, args, dependants }) {
    let { result, loading } = usePromise(async () => controls(args), dependants);
    if (loading || !result) {
        return <CircularProgress />;
    }
    else {
        let buttons = result.map((control, id) => <ItemControl control={control} key={id}/>);
        return <div className="item-controls flexrow">
            {buttons}
        </div>;
    }
}
