export var Desire;
(function (Desire) {
    Desire[Desire["NEED"] = 0] = "NEED";
    Desire[Desire["GREED"] = 1] = "GREED";
    Desire[Desire["SELL"] = 2] = "SELL";
})(Desire || (Desire = {}));
export const DEFAULT_LOOT_FLAG = { desires: [] };
export const LOOT_FLAG_ID = "LootSheet_MarkLootDesire";
export function buildDesireMap(desires) {
    let map = new Map();
    desires.forEach(x => {
        let playMap = new Map();
        x.players.forEach(({ player, desire }) => playMap.set(player.actorId, desire));
        map.set(x.itemId, playMap);
    });
    return map;
}
