import { setupSettingMenu } from "../Constants/Config";
import { defaultAlchemySettings } from "../Item/Alchemy/AlchemySettings";
import SettingsMenu from "./SettingsMenu";
const defaultSetting = {
    alchemy: defaultAlchemySettings
};
export const RegularSettings = setupSettingMenu({
    key: "Regular",
    label: "General Settings",
    default: defaultSetting,
    sheetOptions: { width: 600, height: 800 },
    name: "Regular Settings",
    restricted: true,
    type: SettingsMenu,
    scope: "world"
});
