import React from "react";
import { NumberFormat } from "./ItemTable";
export const ItemColumnImage = {
    title: "",
    getter: ({ item: i }) => <img width="24px" height="24px" src={i.img}/>
};
export const ItemColumnName = {
    title: "Name",
    getter: ({ item: i }) => i.name,
    sortable: true
};
export const ItemColumnWeight = {
    title: "Weight",
    getter: ({ item: i }) => NumberFormat.format(i.weight()),
    sortable: true
};
export const ItemColumnQty = {
    title: "Qty",
    getter: ({ item: i }) => i.qty(),
    sortable: true
};
export const ItemColumnDefaults = [ItemColumnImage, ItemColumnName, ItemColumnWeight, ItemColumnQty];
