function buildSorter(j) {
    return (a, b) => {
        let aV = j(a);
        let bV = j(b);
        return aV - bV;
    };
}
const cost = buildSorter(j => j.cost);
const time = buildSorter(j => j.time);
export const CostSorter = {
    name: "Cost",
    sorter: combine([cost, time])
};
export const TimeSorter = {
    name: "Time",
    sorter: combine([time, cost])
};
function combine(sorters) {
    return (a, b) => {
        for (let s of sorters) {
            let v = s(a, b);
            if (v != 0)
                return v;
        }
        return 0;
    };
}
